import "./Header.css";
import logo from "../assets/imgs/logo.png";
import menu from "../assets/imgs/menu.png";
import NavBar from "./nav-bar/NavBar";
import { useState } from "react";
import { Project } from "../datatypes/bindings";
import { Chip } from "@mui/material";
import { CatchingPokemon } from "@mui/icons-material";

interface HeaderProps {
  projects: Project[];
}

const HeaderComponent: React.FunctionComponent<HeaderProps> = (
  props: HeaderProps
) => {
  const [isNavBarVisible, setIsNavBarVisible] = useState<boolean>(false);

  const toggleNavBar = () => {
    setIsNavBarVisible(!setIsNavBarVisible);
  };

  return (
    <header>
      <div className="container">
        <div className="col-m-3 text-left no-mobile">
          <div className="header-alerts">
            <Chip
              color="error"
              icon={
                <CatchingPokemon
                  sx={{ transform: "rotate(180deg)" }}
                ></CatchingPokemon>
              }
              label="Welcome to my portfolio"
            ></Chip>
          </div>
        </div>
        <div className="col-m-3 text-center">
          <img id="logo" src={logo} alt="logo" width="64px" />
        </div>
        <div className="col-m-3 text-right">
          <button
            className="btn"
            onClick={() => setIsNavBarVisible(!isNavBarVisible)}
          >
            <img id="menu" src={menu} alt="menu" width="25px" />
          </button>
        </div>
      </div>
      <NavBar
        projects={props.projects}
        isNavBarVisible={isNavBarVisible}
        closeNavBar={toggleNavBar}
      />
    </header>
  );
};

export default HeaderComponent;
