import { Button, Chip, Icon } from "@mui/material";
import "../body/list-project/List.css";
import { Project } from "./bindings";
import { Launch } from "@mui/icons-material";

export interface SliderItemProps {
  project: Project;
}

const ListItemComponent: React.FunctionComponent<SliderItemProps> = (
  props: SliderItemProps
) => {
  const getImgBackgroundLogo = () => {
    return {
      background: `url('${props.project.img}')`,
      backgroundPosition: "center",
      backgroundSize: "cover",
    };
  };

  return (
    <>
      <div className="slider-item">
        <div className="project-logo" style={getImgBackgroundLogo()}></div>
        <div className="project-title">
          <h2>{props.project.name}</h2>
        </div>
        <textarea
          value={props.project.description}
          className="project-description"
          readOnly
        ></textarea>
        {props.project.technologies.map((tech) => (
          <Chip
            key={tech}
            label={`#${tech}`}
            color="secondary"
            variant="outlined"
            sx={{ margin: "2px" }}
          />
        ))}

        <div className="button-container">
          <Button
            variant="contained"
            color="primary"
            fullWidth
            onClick={() => window.open(props.project.url, "_blank")}
          >
            <Icon component={Launch} sx={{ marginRight: "8px" }}></Icon>
            View project
          </Button>
        </div>
      </div>
    </>
  );
};

export default ListItemComponent;
