import CloseIcon from "@mui/icons-material/Close";
import { Project, socialMedias } from "../../datatypes/bindings";
import "./NavBar.css";

interface NavBarProps {
  projects: Project[];
  isNavBarVisible: boolean;
  closeNavBar: () => void;
}

const NavBar: React.FunctionComponent<NavBarProps> = (props: NavBarProps) => {
  const getAnimationCss = () => {
    return `0.5s ease-in 0s 1 ${
      props.isNavBarVisible ? "navBarEffectIn" : "navBarEffectOut"
    }`;
  };
  return (
    <>
      <div
        id="navBar"
        style={{
          visibility: props.isNavBarVisible ? "visible" : "hidden",
          animation: getAnimationCss(),
        }}
      >
        <div id="navBar-container">
          <div className="row">
            <div id="navBar-header">
              <button className="btn" onClick={props.closeNavBar}>
                <CloseIcon />
              </button>
            </div>
          </div>
          <div className="row full-h">
            <div className="col">
              <nav>
                <ul>
                  <li key="title">
                    <span className="title">Explore Rene's portfolios</span>
                  </li>
                  {props.projects
                    .filter((project) => project.pin)
                    .map((project) => (
                      <li key={project._id}>
                        <a
                          href={project.url}
                          className="menu-item"
                          target="_blank"
                        >
                          {project.name}
                        </a>
                      </li>
                    ))}
                </ul>
              </nav>

              <div className="social-media">
                <nav>
                  <ul>
                    {socialMedias.map((sm) => (
                      <li key={sm.name}>
                        <a href={sm.url} target="_blank">
                          {sm.name}
                        </a>
                      </li>
                    ))}
                  </ul>
                </nav>
              </div>
            </div>
            <div className="col no-mobile">
              <div id="navBar-background"></div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default NavBar;
