import { ENV } from "./env.enum";

export class ApiClient {
  private baseURL: string = "http://localhost:5000";
  private headers: { [header: string]: string } = {
    "content-type": "application/json;charset=UTF-8",
  };
  constructor(env: ENV) {
    if (env === ENV.LOCAL) {
      this.baseURL = "http://localhost:5000";
    } else {
      this.baseURL = "https://rennyjr-portfolio.onrender.com";
    }
  }

  async getProjects(): Promise<Response> {
    return fetch(`${this.baseURL}/projects`, {
      method: "GET",
      headers: this.headers,
    });
  }

  async getUser(): Promise<Response> {
    return fetch(`${this.baseURL}/user/login`, {
      method: "POST",
      headers: this.headers,
      body: JSON.stringify({
        username: "Rene Rodriguez L",
        password: "pelemarley23",
      }),
    });
  }

  async getProjectsByQuery(
    projectName?: string,
    projectTechs?: string[]
  ): Promise<Response> {
    return fetch(`${this.baseURL}/projects/query`, {
      method: "POST",
      headers: this.headers,
      body: JSON.stringify({
        name: projectName,
        technologies: projectTechs,
      }),
    });
  }
}
